.image {
    display: grid;
    grid-row-gap: 20px;
    width: var(--image-size);
    height: var(--image-size);
    background-color: white;
    border-radius: 12px;
    border: none;
    outline: none;
    justify-items: center;
    padding: 30px var(--image-padding);
    font-family: inherit;
    cursor: pointer;
    @include mobile {
        width: var(--image-size-mobile);
        height: var(--image-size-mobile);
    }
    &--no-padding {
        padding: 30px 0;
    }
    &.selected {
        box-shadow: 0px 0px 16px 13px var(--active-color);
    }

    & > .foto {
        width: 50%;

        & img {
            max-width: 100%;
            max-height: 100%;
        }
    }

    // & > .link {
    //     //@extend .oswald;
    //     color: var(--active-color);
    //     text-decoration: none;
    // }
}
